import { Controller } from '@ali/4ever-cangjie';
import isSidebarVisible from '../queries/isSidebarVisible';
// eslint-disable-next-line import/no-cycle
import { setSidebarVisible } from '../actions';

export default function setActiveId(
  controller: Controller,
  contentId: string | undefined,
  isAutoFocus = true,
) {
  const isSidebarShowing = isSidebarVisible(controller);
  controller.setData({
    ...controller.value.data,
    _commentActiveMarkId: contentId,
    _commentIsAutoFocus: isAutoFocus,
  });
  if (contentId && !isSidebarShowing) {
    controller.run('onAction', setSidebarVisible(true));
  }
  return controller;
}
